<template lang="pug">
  .column('v-on:mouseover'="active = false",'v-on:mouseout'="active = true")
    .d
      .flex-center.boxs(:style="url" :class="classObject")
        h2.title(v-if="active") {{this.Item.title}}
        p.p(v-else v-for="p in Item.p" v-html="p")

</template>

<script>
export default {
  name: 'boxs',
  props: ['Item'],
  data () {
    return {
      active: true,
      url: {
        backgroundImage:`url(${this.Item.url})`
      },
      clase: this.Item.className
    }
  },
  computed: {
    classObject: function () {
      return {
        active: this.active,
        blanco: !this.active,
        'fadeIn': !this.active,
        'gray': this.clase === 'gray',
        'white': this.clase === 'white'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
$blanco: #ffffff;
$verde: #00811e;

.flex-center{
  align-items: center;
  flex-direction: column;
}

.p{
  margin-bottom: 1em;
  &>span{
    font-weight: bold;
  }
}

span{
  font-weight: bold;
}

.d{
  height: 400px;
  position: relative;
}

.column{
  padding-left: 0px;
  padding-right: 0px;
}

.active{
  background-image: initial!important;
  background-color: $verde;
  opacity: 1!important;
}

.blanco{
   &>h2{
    color: $blanco!important;
  }
  &>p{
    color: $blanco!important;
  }
}

.green{
  background-color: $verde;
}

.boxs{
  height: 400px;
  padding: 3em 1.5em;
  //padding-bottom: 32%;
  color: $blanco;
  text-align: justify;
  background-size: cover;
  background-repeat: no-repeat;
  &>h2{
    color: $blanco;
    font-size: 2em;
    font-weight: 300;
    line-height: 1.13;
    letter-spacing: -0.1px;
    text-align: center;
  }
  &>p{
    font-size: 1em;
  }
}

.gray{
  background-color: #f1f1f1;
  &>h2{
    color: #747474;
  }
  &>p{
    color: #747474;
  }
}

.white{
  background-color: $blanco;
  &>h2{
    color: $verde;
  }
  &>p{
    color: $verde;
  }
}

@media (max-width:600px){
  .boxs{
   // height: 30vh;
  }
}
</style>


