<template lang="pug">
  .banner.align(:style="url")
    .in
      h1 {{text}}
    .in2
      p {{p}} #[strong {{text2}}]
      .flex-center
        .info Más Información

</template>

<script>
export default {
  name: 'banner',
  props: ['Item'],
  data () {
    return {
      url: {
        backgroundImage:`url(${this.Item.url})`
      },
      text: this.Item.text,
      p: this.Item.p,
      text2: this.Item.text2
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
$blanco: #ffffff;

.info{
  padding: 0px .4em;
  border: 1px solid $blanco;
  border-radius: 7px;
  margin-top: 2em;
  color: $blanco;
  cursor: pointer;
}

.banner{
  height: 80vh;
  background-position: center;
  background-size: cover;
  align-items: center;
  font-size: 1.4em;
  &>.in2{
    padding-left: 2em;
    width: 27%;
    &>h1{
      font-size: 2em;
      font-weight: bold;
      line-height: 1.2;
      letter-spacing: -0.5px;
      color: $blanco;
      text-align: left;
      margin-bottom: .5em;
    }
    &>p{
      font-size: .8em;
      font-weight: 300;
      line-height: 1.36;
      letter-spacing: -0.1px;
      color: $blanco;
      text-align: justify;
      &>strong{
        color: $blanco;
      }
    }
  }
  &>.in{
    padding-left: 2em;
    width: 40%;
    padding-top: 10%;
    &>h1{
      font-size: 1.8em;
      font-weight: bold;
      line-height: 1.2;
      letter-spacing: -0.5px;
      color: $blanco;
      text-align: left;
      margin-bottom: .5em;
    }
    &>p{
      font-size: .8em;
      font-weight: 300;
      line-height: 1.36;
      letter-spacing: -0.1px;
      color: $blanco;
      text-align: justify;
      &>strong{
        color: $blanco;
      }
    }
  }
}


@media (max-width:600px){
  .banner{
    font-size: 1em;
    &>.in{
      width: 100%;
      padding: 2em;
      &>h1{
        text-align: center;  
      }
      &>p{
        text-align: center;  
      }
    }
  }
}
@media (max-width:1200px){
  
  .banner{
    height: 80vh;
    width: 100%;
    font-size: 1.3em;
    &>.in{
      &>h1{
        font-size: 1.4em;
      }
    }
    &>.in2{
      width: 100%;
      padding-left: 3%;
      &>h1{
        font-size: 1.8em;
      }
      &>p{
        width: 95%;        
      }
    }
  }
}

</style>



