<template lang="pug">
  div
    Navbar
    Banner(name="",:Item="banner")
    .columns.margin
      Boxs(name="",:Item="item" v-for="(item,index) in boxs" :key="index")
    Neckle
    Description(nae="",:Item="description")
    Foot(name="",:Item="foot")
</template>

<script>
import Navbar from '@/components/Navbar/Navbar.vue'
import Banner from '@/components/Banner/Banner.vue'
import Boxs from '@/components/Boxs/Boxs.vue'
import Neckle from '@/components/Neckle/Neckle.vue'
import Description from '@/components/Description/Description.vue'
import Foot from '@/components/Footer/Footer.vue'
const banner = {
  url: 'static/img/banner.png',
  text: '¡Desde el 2007 activamente limpiando el mundo!',
  p: 'Somos una empresa dedicada a la Gestión Integral de Residuos y materiales peligrosos, así como a la consultoría y ejecución de proyectos enfocados en temas ambientales con sede en Costa Rica.',
}

const box = {
  title: 'Gestión y tratamiento de residuos',
  p: ["<span class='span'>Residuos peligrosos:</span> todo material que tenga características que lo hagan corrosivo, reactivo, explosivo, tóxico, inflamable o bio infeccioso. <br><small>*Cabe aclarar que Ecoway no gestiona directamente materiales radiactivos, explosivos o bio infecciosos.</small>",
  "<span class='span'>Residuos no tradicionales:</span> materiales que sin ser peligrosos, por sus características requieren un manejo particular. (Ejemplo: llantas, muebles, línea blanca, equipo electrónico, etc). ",
  "<span class='span'>Residuos reciclables:</span> todo tipo de material no peligroso que es tradicionalmente reutilizable o comercializable. (Ejemplo: papel, cartón, plásticos, etc)."
  ],
  className: "green",
  url: 'static/img/box1.png',
  active: false
}

const box2 = {
  title: 'Almacenamiento y administración de inventarios de materiales peligrosos.',
  p: [
    "Contamos con las condiciones óptimas para el almacenamiento de materiales peligrosos, ya sea por carencia de espacio en sus instalaciones, por su ubicación física o por los riesgos asociados.",
    "Para ello ofrecemos el servicio de bodegaje, custodia, control de inventarios, entregas periódicas contra pedido, recolección de materias primas y/o reactivos peligrosos."
  ],
  className: 'gray',
  url: 'static/img/box2.png',
  active: false
}

const box3 = {
  title: 'Desmantelamiento de instalaciones industriales y remediación de espacios',
  p: [
    "Atendemos las necesidades en materia de desmantelamiento limpio, evitando la posible contaminación asociada a los equipos y materiales relacionados.",
    "Estamos capacitados para atender cualquier necesidad o requerimiento."
  ],
  className: 'white',
  url: 'static/img/box3.png',
  active: false
}

const icon1 = {
  url: 'static/img/handUp.png',
  title: 'Aumentar tus ventajas competitivas'
}

const icon2 = {
  url: 'static/img/martillo.png',
  title: 'Evitar problemas legales'
}

const icon3 = {
  url: 'static/img/people.png',
  title: 'Fortalecer su imagen'
}

const icon4 = {
  url: 'static/img/house.png',
  title: 'Buen Vecino'
}

const icon5 = {
  url: 'static/img/hand.png',
  title: 'beneficios económicos'
}

const icon6 = {
  url: 'static/img/tree.png',
  title: 'controla tu impacto ambiental'
}

const description = {
  title: 'Consultoría ambiental, asesoría y capacitación.',
  p: [
    'Diseño de programas de reciclaje y valorización.',
    'Diseño y elaboración de planes de manejo.',
    'Apoyo en actividades de temática ambiental y de residuos.',
    'Desarrollo de programas de educación ambiental.',
    'Coordinación y logística de eventos de temática ambiental.',
    'Implementación de Sistemas de Gestión Ambiental bajo la Norma ISO 14001.',
    'Diagnósticos sobre la situación ambiental.',
    'Auditorías ambientales.',
    'Coordinación y ejecución de medidas preventivas y/o correctivas.',
  ],
  icon1,
  icon2,
  icon3,
  icon4,
  icon5,
  icon6,
}

const foot = {
  url: 'static/img/footer.jpg'
}
export default {
  name: 'hello',
  components: {Navbar, Banner, Boxs, Description ,Foot, Neckle},
  data () {
    return {
      banner,
      boxs: [box,box2,box3],
      description,
      foot,
      estado: false
    }
  },
  mounted: function () {
    var newImg = new Image
    var newImg2 = new Image
    var newImg3 = new Image
    newImg.src = 'static/img/box1.png'
    newImg2.src = 'static/img/box2.png'
    newImg3.src = 'static/img/box3.png'

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">

.margin{
  margin-bottom: 2em!important;
}

.flex{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
}

.flex-end{
  @extend .flex;
  justify-content: flex-end;
}

.flex-center{
  @extend .flex;
  justify-content: center;
}

.flex-start{
  @extend .flex;
  justify-content: flex-start;
}


h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495E;
}

ul{
  list-style-type: disc!important;
  &>li{
    display: list-item!important;
  }
}

.span{
  font-weight: bold;
}
</style>



