<template lang="pug">
div
  .columns.mar
    .column.is-6
      slick(ref="slick" :options="slickOptions")
        .box
          h2.title {{item.title}}
          ul
            li(v-for="p in item.p") {{p}}
          //p.p(v-for="p in item.p") {{p}}
          .flex-center
            .contact('v-on:click'="next") CONTACTENOS
        Contact

    .column
      .columns.is-fluid
        Icons(name="",:Item="item.icon1")
        Icons(name="",:Item="item.icon2")
      .columns.is-fluid
        Icons(name="",:Item="item.icon3")
        Icons(name="",:Item="item.icon4")
      .columns.is-fluid
        Icons(name="",:Item="item.icon5")
        Icons(name="",:Item="item.icon6")
      .flex-center
        p.stron Más que solo deshacernos de los residuos o reutilizarlos, también realizamos otras funciones que se transformarán en grandes ventajas para ti, y harán, que la tarea de cuidar el ambiente valga aún más la pena. 

</template>

<script>
import Slick from 'vue-slick'
import Icons from '@/components/Description/Icons.vue'
import Contact from '@/components/Contact/Contact.vue'
export default {
  name: 'description',
  components: {Icons, Slick, Contact },
  props: ['Item'],
  data () {
    return {
      item: this.Item,
      slickOptions: {
        slidesToShow: 1,
        dots: false,
        arrows: false
      },
    }
  },
  methods: {
    next() {
      console.log(this.$refs)
      this.$refs.slick.next();
    },
    prev() {
        this.$refs.slick.prev();
    },
    reInit() {
      this.$nextTick(() => {
        this.$refs.slick.reSlick();
      });
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
$blanco: #ffffff;
$verde: #00811e;

.center{
  text-align: center;
}

.stron{
  font-size: .9em;
  letter-spacing: -0px;
  text-align: center;
  color: #747474;
  width: 80%;
}

.columns{
  padding: 0px 5%;
}

.mar{
  margin-bottom: 1.5em!important;
}

.box{
  padding: 1em 10%;
  width: 99%!important;
  left: 3px;
  position: relative;
  border-radius: 12px;
  box-shadow: 0 2px 6px 0 rgba(138, 138, 138, 0.5);
  border: solid 1px #c5c5c5;
  margin-bottom: 1em;
  margin-right: 1em;
  &>h2{
    color: #747474;
    margin: 1em 0px;
  }
  &>p{
    font-size: 1em;
    letter-spacing: -0px;
    text-align: left;
    color: #747474;
    margin-bottom: 2em;
    line-height: 1.6;
  }
}

.contact{
  margin-top: 1em;
  color: $verde;
  border: 1px $verde solid;
  border-radius: 7px;
  font-size: 2em;
  font-weight: 600;
  letter-spacing: -0px;
  padding: 0px 3em;
  cursor: pointer;
  margin-bottom: 1.5em;
}

.footer{
  margin-top: -.5em;
  background-color: #1c1c1c;
}

@media (max-width:500px){
  .contact{
    padding: 0px 1em;
  }
}
@media (max-width:1200px){

}

</style>



