<template lang="pug">
.box
  h2.title No dudes en Contactarnos
  form.form
    .field
      label.label Email
      .control
        input.input(type="email")
    .field
      label.label Nombre Apellido
      .control
        input.input(type="text")
    .field
      label.label Telefono
      .control
        input.input(type="number")
    .enviar CONTACTENOS

</template>

<script>
export default {
  name: 'contact',
  props: ['Item'],
  data () {
    return {
      item: this.Item
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
$blanco: #ffffff;
$verde: #00811e;

.label{
  color: $blanco;
}

.box{
  width: 99%;
  border-radius: 12px;
  box-shadow: 0 2px 6px 0 rgba(138, 138, 138, 0.5);
  border: solid 1px #c5c5c5;
  margin-bottom: 1em;
  background-color: $verde;
  &>h2{
    color: $blanco;
  }
  &>p{
    font-size: 1em;
    letter-spacing: -0px;
    text-align: left;
    color: #747474;
    margin-bottom: 2em;
  }
}

.enviar{
  margin-top: 1em;
  border: 1px solid $blanco;
  color: $blanco;
  background-color: $verde;
  border-radius: 7px;
  font-size: 2em;
  font-weight: 600;
  letter-spacing: -0px;
  padding: 0px 3em;
  cursor: pointer;
  text-align: center;
}


@media (max-width:600px){
  .enviar{
    padding: 0px;
  }
}
@media (max-width:1200px){

}

</style>



