<template lang="pug">
  .column
    .center
      img.img(:src="item.url")
      h4.h4 {{item.title}}
      p.p {{item.p}}

</template>

<script>
export default {
  name: 'icons',
  props: ['Item'],
  data () {
    return {
      item: this.Item
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
$blanco: #ffffff;
$verde: #00811e;

.center{
  text-align: center;
}

.img{ width: 66px; }

.h4{
  font-size: 1.1em;
  font-weight: bold;
  line-height: 1.22;
  letter-spacing: -0px;
  color: #515050;
}

.p{
  font-size: .9em;
  line-height: 1.47;
  letter-spacing: -0px;
  color: #747474;
}


@media (max-width:600px){

}

@media (max-width:1200px){

}

</style>



