<template lang="pug">
div
  nav.navbar(role="navigation" aria-label="main navigation")
    .navbar-brand
      a.navbar-item
        img.logo(:src="'static/img/logo.png'" alt="Ecoway" )
      button.button.navbar-burger(data-target="navMenu" v-on:click="Click")
        span
        span
        span
    a.navbar-menu#navMenu
      .navbar-start
      .navbar-end
        a.navbar-item(v-for="item in items") {{item}}
        a.navbar-item
          .boxs.flex
            .left CONTACTENOS
            .right (888) 780-0110

</template>
<script>

export default {
  name: 'Navbar',
  data () {
    return {
      items: ['Servicios', 'Beneficios', 'Nuestros Clientes']
    }
  },
  methods: {
    Click (el){
      document.querySelectorAll('#navMenu')[0].classList.toggle('is-active')
      document.querySelectorAll('.navbar-burger')[0].classList.toggle('is-active')
    }
  }
}
</script>

<style lang="scss" scoped>
$verde: #00811e;
$blanco: #ffffff;

.navbar-item:hover{
  color: $verde!important;
  background-color: white;
}

nav{
  height: 20vh;
  padding: 0px 5%;
  padding-left: 3%;
}

.boxs{
  border: 1px solid $verde;
  //padding: 1em 0px;
}

.left{
  background-color: $verde;
  color: $blanco;
  font-size: 1.2em;
  font-weight: 600;
  letter-spacing: -0.1px;
  color: #ffffff;
  padding: .3em .4em;
}

.right{
  font-size: 1.2em;
  font-weight: 600;
  letter-spacing: -0.1px;
  color: #7d7d7d;
  padding: .3em .4em;
}

.navbar-item{
    font-size: 1em;
    font-weight: 600;
    letter-spacing: 0px;
    text-align: left;
    color: #989898;
}

.flex{
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
}

.flex-end{
  @extend .flex;
  justify-content: flex-end;
  background-color: $blanco;
  padding-top: 1em;
}

.logo{
  width: 88px;
  height: 95px;
  max-height: initial !important;
}


@media (max-width:1023px){
  .logo{
    //width: 200px;
  }
  .car-shop{
    position: fixed!important;
    bottom: 10px;
    right: 10px;
    top: initial !important;
    z-index: 100;
  }
}
</style>


